body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.container {
  max-width: 1480px;
}

.headDiv {
  margin-top: 20px;
  margin-bottom: 40px;
}

.navRow {
  position: absolute;
  background-color: cornflowerblue;
  padding: 0px 20px 0px 20px;
  height: 96px;
  width: 100%;
}

.footer {
  position: fixed;
  height: 60px;
  width: 100%;
  font-size: 20px;
  bottom: 0px;
  text-align: center;
  background-color: #343a40;
  color: white;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.navbar {
    padding-left: 0px;
    padding-right: 0px;
    display: block;
    height: 100px;
}


form {
    position: relative;
    top: 20px;
}

.delete {
    background-color: transparent;
    border-radius: 20px;
    border-style: solid;
    border-color: #343a40;
}

